<template>
    <div @touchstart="scrollFixStart" @touchmove="scrollFixMove" class="userlist-pasenge">
        <div class="cell-wapper">
            <custom-cell v-for="client in clientsdata" :headimgurl="client.headimgurl" :conndate="client.conndate" :phone="client.phone||'暂无'" :username="client.username"></custom-cell>
        </div>
        <empty-bg :msg="emptyMsg" v-show="showEmpty==true"></empty-bg>
    </div>
</template>

<script>
import { mapState } from "vuex";
import vImg from "../lazyloadimg/lazyimg.vue";
import EmptyBg from "../popupBox/emptyBg.vue";
import customCell from "../public/customCell.vue";
export default {
  name: "userlist",
  data() {
    return {
      showEmpty: false,
      emptyMsg: "当前没有记录哦~",
      clientsdata: []
    };
  },
  components: {
    vImg,
    EmptyBg,
    customCell
  },
  created() {
    // 请求服务器数据
    let requestUserid = this.$route.params.userid;
    if (!requestUserid || requestUserid == "") requestUserid = this.userid;
    var urlstr = this.path + "php/suncode/dealfuc.php";
    this.$http
      .post(urlstr, {
        user: requestUserid,
        fuc: "clientlist"
      })
      .then(response => {
        this.clientsdata = response.body;
        if (this.clientsdata.length > 0) {
          this.showEmpty = false;
        } else {
          this.showEmpty = true;
        }
        this.$store.dispatch("toggleLoading", false);
      })
      .catch(response => {
        //请求出错处理
        console.log(response);
        this.$store.dispatch("toggleLoading", false);
      });
  },
  computed: {
    ...mapState(["userid", "path"])
  },
  methods: {
    scrollFixStart(e) {
      var el = e.currentTarget;
      if (el.scrollTop === 0) {
        el.scrollTop = 1;
      } else if (el.scrollHeight == el.scrollTop + el.offsetHeight) {
        el.scrollTop = el.scrollTop - 1;
      }
    },
    scrollFixMove(e) {
      console.log("targetmove");
      var el = e.currentTarget;
      if (el.scrollHeight > el.clientHeight) {
        e.stopPropagation();
      }
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.userlist-pasenge {
  overflow: auto;
  overflow-x: hidden;
  width: 100%;
  flex: 1;
  padding-top: 0.3rem;
  box-sizing: border-box;
  background-color: #eee;

  .cell-wapper {
    // min-height: 100%;
  }
}
</style>



